<template>
  <div class="hello">
    <img class="img" src="../assets/imgs/组 4.png" alt="">
    <div class="tell">
      <div class="phone">
        <img src="../assets/imgs/手机.png" style="width: 15px;height: 22px;" alt="">
        <input type="text" placeholder="请填写手机号码" v-model="phone">
      </div>
      <div class="test">
        <img src="../assets/imgs/密码 (1).png" style="width: 17px;height: 22px;" alt="">
        <input style="width: 80%;" type="text" placeholder="请填写验证码" v-model="pass" />
        <div class="submit" @click="getcode">{{content}}</div>
      </div>
      <div class="password">
        <img src="../assets/imgs/密码.png" style="width: 17px;height: 22px;" alt="">
        <input type="password" placeholder="请填写密码" v-model="password" />
      </div>
      <div class="againpassword">
        <img src="../assets/imgs/密码.png" style="width: 17px;height: 22px;" alt="">
        <input type="password" placeholder="请再次填写密码" v-model="againpassword" />
      </div>
    </div>
    <div class="now">
      <button class="register" @click="nowregister">立&nbsp;即&nbsp;注&nbsp;册</button>
      <div class="fs">注册即视为同意<span style="color: #fec12b" @click="jump">《用户协议》</span></div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios';
   let search = window.location.search
   console.log(search);

    let searchArray = search.slice(1);
    let invite = '';
    let paramsArray = searchArray.split('&');
    paramsArray.forEach((item) => {
        let paramItem = item.split('=');
        if (paramItem[0] == 'invite') {
            invite = paramItem[1]
        }
    });
  export default {
    name: 'HelloWorld',
    props: {
      msg: String
    },
    data() {
      return {
        phone: '',
        pass: '',
        code: '',
        password: '',
        againpassword: '',
        validate_key: '',
        modalAddShow: false,
        content: '获取验证码',
        totalTime: 60
      }
    },
    methods: {
      getcode() {
        let _this = this
        if (_this.phone == '') {
          alert('手机号码不能为空')
        } else if (!(/^1[3|4|5|8][0-9]\d{4,8}$/.test(_this.phone))) {
          alert("手机号码有误，请重填");
          return false;
        } else {
          axios.post("https://a895.mxnt.net/api/verificationCodes", {
            phone: _this.phone
          }).then(res => {
            if (res.status !== 200 || res.data.code != 200) {
              alert(res.data.msg)
            }
            let data = res.data
            _this.code = data.code
            _this.validate_key = data.data.validate_key
            let clock = window.setInterval(() => {
              this.totalTime--
              _this.content = _this.totalTime + 's'
              if (this.totalTime < 0) {         //当倒计时小于0时清除定时器
                window.clearInterval(clock)
                this.content = '获取验证码'
                this.totalTime = 60
              }
            },1000);
          }).catch(err => {
            console.log(err);
          })
        }
      },
      nowregister() {
        let _this = this
        if (_this.phone == '' || _this.pass == '' || _this.password == '' || _this.againpassword == '') {
          alert('信息不能为空')
        } else if (!(/^1[3|4|5|8][0-9]\d{4,8}$/.test(_this.phone))) {
          alert("手机号码有误，请重填");
          return false;
        } else if (!(/^[0-9A-Za-z]{6,}$/.test(_this.password))) {
          alert('密码不能少于6位数')
        } else if (_this.againpassword !== _this.password) {
          alert('两次密码不一致')
        } else {
          axios.post("https://a895.mxnt.net/api/users", {
            validate_key: _this.validate_key,
            code: _this.pass,
            password: _this.password,
            invite:this.$route.query.invite
          }).then(res => {
            if (res.status !== 200 || res.data.code != 200) {
              alert(res.data.msg)
            } else {
              console.log(111);
              document.location.href = 'http://d.7short.com/aey1';
            }
          }).catch(err => {
            console.log(err);
          })
        }
      },
      jump() {
        this.modalAddShow = true
        axios.get("https://a895.mxnt.net/api/about_us/0", {}).then(res => {
          if (res.status !== 200) {
            alert("网络错误")
          } else {
            let text = res.data
            this.$router.push({
              name: 'agreement',
              params: {
                text: text
              }
            })
          }
        }).catch(err => {
          console.log(err);
        })
      }
    },
  }
</script>

